	module.exports = {"data":{"markdownRemark":{"html":"<p>기사 작성 및 정부 이미지에 관한 이야기 ​​:</p>\n<p>가치에 중점을 두어 정의와 다섯 가지 요소를 제안합니다. 정의는 다음과 같습니다. 정부의 이미지는 특정 정부 시스템의 기능에 대한 전반적인 성과를 의미합니다. 정부 이미지의 평가는 두 가지 측면에서 나온다 : 첫째, 자체 운영의 정부 시스템 자체의 전반적인 질, 또는 정부 규제의 품질 지표, 둘째, 정부 시스템의 승인 프로세스 및 공공 가치 평가에서 얻은 결과. 색인.</p>\n<p>요소는 가치, 신뢰성, 효율성, 공정성 및 정직성을 나타냅니다. 그중 신뢰성은 매우 중요합니다. 정부는 자체적 인 신뢰를 중요하게 생각해야한다. 정부의 신뢰성과 회사의 신뢰도에는 유사점이 있습니다. 기업의 명성은 기업 제품과 시장 간의 관계에 의해 결정됩니다. 신용 할만한 기업이 없다면 당연히 시장이 없으며 기업 자체는 자연스럽게 존재와 발전의 여지가 없습니다. 정부의 신뢰도 또한 동일하다. 정부가 신뢰를 얻지 못하면 국민의 심리적 정체성과지지를 잃을 것이며, 정부의 에너지는 제한 될 것이다. 정부의 신뢰성은 무엇인가? 전체적으로 정부의 신뢰는 정부가 통제하는 사람들에 대한 정부의 전체적인 헌신을 이행하는 것입니다. 중국은 사회주의 국가이며 정부를 관리하는 것이 최고 목표이며 집권당과 정부는 국민들에게 봉사하겠다는 약속을했다 이것은 또한 중국 공산당이 오랫동안 싸운 목표이기 때문에 모든 정부 차원 모두는이 전반적인 약속을 실천할 의무가 있습니다. 정부의 약속이 일상적인 관리와 정부 활동에서 성취 될 수 있는지 여부는 정부의 신뢰성에있어 가장 기본적인 요소입니다. 정부의 전반적인 약속은 시스템을 구성하는 모든 수준의 기관에 의해 충족되어야하며 고위급 조직 이건 기본 수준 조직 이건 상관없이 각 정부 기관은 선임 간부 이건 중등 간부 이건 관계없이 자체 실천에서 전반적인 결과를 달성해야합니다. 약속 해. 전반적인 약속은 모든 정부 차원에서 이루어지며 모든 단계의 정부는 각자의 활동에서 자신의 약속을 이행하므로 모든 수준의 정부 요원은 반드시 실천하고 약속해야합니다. 이런 식으로 만 정부의 완벽한 이미지를 형성 할 수 있습니다. 이 기사가 없으면 정부 관료들은 사람들을 전심으로 봉사 할 수없고, 사람들의 관심사를 가장 높은 위치에 두거나, 자신의 이익을 최우선으로 생각하거나, 의무를 게을리하지 않을 수 있습니다. 가장 기본적인 전제 조건이 존재하지 않기 때문에 당연히 정부의 이미지는 크지 않을 것입니다. 자.</p>\n<p>정치 헌신은 발전 할만한 가치가있는 개념이며 중국 정치 철학 확립을위한 논리적 출발점이 될 수 있습니다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0059.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0059.md","frontmatter":{"title":"3월 15일 화요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0059.md absPath of file >>> MarkdownRemark"}}

